import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { TransitionGroup, CSSTransition } from 'react-transition-group'
// css
import './hotDeals.scss'

const HotDeals = ({ dataSlider }) => {
    const [ activeSlide, setActiveSlide ] = useState(0) 
    const maxSlide = dataSlider.length;

    const changeActiveSlide = () => {
        let slide = activeSlide + 1;
        if (slide > (maxSlide - 1)) {
            slide = 0;
        }
        setActiveSlide(slide);
    }

    return (
        <div id="hot_deals">
            <span onClick={changeActiveSlide} 
                className="material-icons arrow-next" 
                title="Siguiente">arrow_forward_ios</span>

            <TransitionGroup>
                <CSSTransition
                    timeout={1000}
                    key={activeSlide}
                    classNames="hot-deal">
                    <div>
                        {
                            dataSlider.map((slide, index) => (
                                <div key={index} 
                                    style={activeSlide !== index ? {display: 'none'} : {} }
                                    className="hot-dealts-container">
                                    <div className="left">
                                        <div className="container-image">
                                            <img src={require(`assets/img/hot-deals/${slide.image}`)} className="image" alt=""/>
                                        </div>
                                    </div>
                                    <div className="right">
                                        <div className="data">
                                            <h2 className="first-title">{slide.firstTitle}</h2>
                                            <h2 className="second-title">{slide.secondTitle}</h2>
                                            <p className="description">{slide.description}</p>
                                            <div className="container-button">
                                                <a className="default-btn" target="_blank" rel="noopener noreferrer"
                                                    href={`https://wa.me/573214682422?text=${slide.order}`}>
                                                    Ordenar Ahora
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </CSSTransition>
            </TransitionGroup>
        </div>
    )
}

HotDeals.propTypes = {
    dataSlider: PropTypes.array
}

HotDeals.defaultProps = {
    dataSlider: [
        {
            image: 'default-hot-deal.png',
            firstTitle: 'Oferta Especial',
            secondTitle: 'Arroz + Gaseosa + Papas',
            description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Quae, animi veniam. Beatae, doloremque nulla sit sequi excepturi molestias temporibus neque?',
            order: '¡Hola! Estoy interesado en la oferta - Arroz + Gaseosa + Papas -'
        },
        {
            image: 'default-hot-deal.png',
            firstTitle: 'Oferta Especial',
            secondTitle: 'Arroz',
            description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Quae, animi veniam. Beatae, doloremque nulla sit sequi excepturi molestias temporibus neque?',
            order: '¡Hola! Estoy interesado en la oferta - Arroz + Gaseosa + Papas -'
        }
    ]
}

export default HotDeals
