import React from 'react'
import MainSlider from 'components/MainSlider'
import PlatesGrid from 'components/Menu/PlatesGrid'
import HotDeals from 'components/HotDeals'
import CustomerReviews from 'components/CustomerReviews'
import Footer from 'components/Footer'
import { Link } from 'react-router-dom'
import LazyLoad from 'react-lazyload';
// css
import './home.scss'

// Menu - Plates
import plates from 'data/menu.json'
// Hot Deals
import hot_deals from 'data/hot_deals.json'

const HomePage = () => {
    const dataSlider = [
        {
            smallTitle: 'Prueba nuestra',
            bigTitleC1: 'Lechona',
            bigTitleC2: 'Especial',
            description: 'Lechona mixta de carne de cerdo con pollo.',
            image: 'main-slider/test.png',
            order: '¡Hola!, Estoy interesado en *Lechona Especial mixta de carne de cerdo con pollo.*'
        },
        {
            smallTitle: 'Prueba Lechona',
            bigTitleC1: 'Tradicional',
            bigTitleC2: '',
            description: 'Lechona con carne de cerdo pulpa sin hueso.',
            image: 'main-slider/test.png',
            order: '¡Hola!, Estoy interesado en *Lechona Tradicional con carne de cerdo pulpa sin hueso.*'
        },
        {
            smallTitle: 'Prueba Lechona',
            bigTitleC1: 'Super',
            bigTitleC2: 'Especial',
            description: 'Lechona con huevos de codorniz, queso reinoso, salchicha ranchera, carne de cerdo, pollo y uvas pasas.',
            image: 'main-slider/default-image.png',
            order: '¡Hola!, Estoy interesado en *Lechona Super Especial Huevos de codorniz, queso reinoso, salchicha ranchera, carne de cerdo, pollo y uvas pasas (opcional).*'
        }
    ];
    
    return (
        <div className="home">
            {/* Slider */}
            <MainSlider dataSlider={dataSlider} />
            {/* Menú */}
            <div className="explore-menu lc-container df-padding-3">
                <h1 className="lc-default-title">
                    Explorar <span className="highlighted">Carta</span>
                    <span className="back">Explorar Carta</span>
                </h1>
                <PlatesGrid plates={plates} xs={3} sm={4} lg={6} />

                <div className="container-menu-button">
                    <Link exact="true" to="/menu" className="default-btn btn-menu">
                        Ver Carta
                    </Link>
                </div>
            </div>
            {/* Hot Deals */}
            <div className="hot-deals lc-container df-padding-3">
                <h1 className="lc-default-title">
                    Ofertas <span className="highlighted">Especiales</span>
                    <span className="back">Ofertas Especiales</span>
                </h1>
                <HotDeals dataSlider={hot_deals} />
            </div>
            {/* Customer Reviews */}
            <div className="customer-reviews lc-container df-padding-3">
                <h1 className="lc-default-title title">
                    Opinión <span className="highlighted">de los Clientes</span>
                    <span className="back">Opinión de los Clientes</span>
                </h1>
                <CustomerReviews />
            </div>
            {/* Open Gallery */}
            <LazyLoad 
                height={200}>
            <div className="open-gallery">
                <div className="container-imgs">
                    <div className="images">
                        <img src={require(`assets/img/gallery/1.jpg`)} className="image mobile" alt=""/>

                        <img src={require(`assets/img/gallery/2.jpg`)} className="image tablet" alt=""/>
                        <img src={require(`assets/img/gallery/3.jpg`)} className="image tablet" alt=""/>
                        
                        <img src={require(`assets/img/gallery/4.jpg`)} className="image desktop" alt=""/>
                        <img src={require(`assets/img/gallery/5.jpg`)} className="image desktop" alt=""/>

                        <img src={require(`assets/img/gallery/6.jpg`)} className="image desktop-lg" alt=""/>
                        <img src={require(`assets/img/gallery/7.jpg`)} className="image desktop-lg" alt=""/>
                        <img src={require(`assets/img/gallery/8.jpg`)} className="image desktop-lg" alt=""/>
                    </div>
                    <div className="text">
                        <p>
                            Vivamos juntos <br/>los mejores<br/> <span>#momentosLechoncitos</span>
                        </p>
                    </div>
                </div>
                <div className="container-button">
                    <Link exact="true" to="/galeria" className="default-btn btn-open-gallery">
                        Ver Galería
                    </Link>
                </div>
            </div>
            </LazyLoad>
            <br/><br/><br/>
            <Footer />
        </div>
    )
}

export default HomePage