import React, { useState, useEffect } from 'react';
import {
    BrowserRouter as Router,
    Route,
    NavLink,
    Switch
} from 'react-router-dom';
import './header.scss';
// Pages
import HomePage from 'components/HomePage'
import GalleryPage from 'components/GalleryPage'
import AboutUsPage from 'components/AboutUsPage';
import MenuPage from 'components/MenuPage';

const Header = () => {
    const [ toggleMenu, setToggleMenu ] = useState(false);
    const [ lastScroll, setLastScroll ] = useState();
    const [ headerVisibility, setHeaderVisibility ] = useState('')

    useEffect(() => {
        document.addEventListener('scroll', handleScroll);
        
        return () => {
            document.removeEventListener('scroll', handleScroll);
        }
    }, [lastScroll])

    const handleToggleMenu = () => {
        setToggleMenu(!toggleMenu)
    }

    const handleScroll = () => {
        window.scrollY > lastScroll ? setHeaderVisibility('hidden-header') : setHeaderVisibility('')
        setLastScroll(window.scrollY)
    }


    return (
        <Router>
            {/* lc => lechoncitos */}
            <header 
                id="lc_header" 
                className={`lc-header-black ${headerVisibility} ${toggleMenu ? 'no-hidden ' : ''}`}>
                <div className="lc-container-header lc-container">
                    <div className="left">
                        <img className="logo" src={require('assets/img/logo.svg')} alt="Lechoncitos"/>
                    </div>
                    <div className="center">
                        <div
                            className={`${toggleMenu ? 'show-menu ' : ''}container-items`} >
                            <ul className="items">
                                <li>
                                    <NavLink exact activeClassName="item-active" to="/">
                                        Inicio
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink exact activeClassName="item-active" to="/menu">
                                        Carta
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink exact activeClassName="item-active" to="/galeria">
                                        Galería
                                    </NavLink>
                                </li>
                                <li>
                                    <a href="#">
                                        Blog
                                    </a>
                                </li>
                                <li>
                                    <NavLink exact activeClassName="item-active" to="/quienes-somos">
                                        Lechoncitos
                                    </NavLink>
                                </li>
                            </ul>
                        </div>
                        {/* <button className="btn-order-now">
                            Ordenar Ahora
                        </button> */}
                    </div>
                    <div className="right">
                        <span
                            onClick={handleToggleMenu} 
                            id="icon_menu" 
                            className="material-icons">menu</span>
                            <a href="https://wa.me/573214682422?text=Hola, estoy interesado en..." className="btn-order-now" target="_blank" rel="noopener noreferrer">
                                Ordenar Ahora
                            </a>
                            {/* <button className="btn-order-now">
                                Ordenar
                        </button> */}
                    </div>
                </div>
            </header>
            <Switch>
                <Route exact path="/" component={HomePage} />
                <Route exact path="/galeria" component={GalleryPage} />
                <Route exact path="/quienes-somos" component={AboutUsPage} />
                <Route exact path="/menu" component={MenuPage} />
            </Switch>
        </Router>
    )
}

export default Header