import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { TransitionGroup, CSSTransition } from 'react-transition-group'
// css
import './mainSlider.scss'

const MainSlider = ({ dataSlider }) => {
    const [ activeSlide, setActiveSlide ] = useState(0) 
    const maxSlide = dataSlider.length;

    const changeActiveSlide = (index) => {
        if (!isNaN(index) && index <= (maxSlide - 1) && index >= 0) {
            setActiveSlide(index);
        } else {
            let slide = index === 'prev' ? activeSlide - 1 : activeSlide + 1;
            if (slide < 0) {
                slide = (maxSlide - 1);
            } 
            if (slide > (maxSlide - 1)) {
                slide = 0;
            }
            setActiveSlide(slide);
        }
    }

    return (
        <div className="main-slider df-padding-2">
            {
                dataSlider.map((slide, index) => (
                    <div key={index} 
                        className="lc-container content-slider"
                        style={activeSlide !== index ? {display: 'none'} : {} }>
                        {/* Left */}
                        <div className="left">
                            {/* Data */}
                            <div className="data">
                                <TransitionGroup>
                                    <CSSTransition
                                        timeout={1000}
                                        classNames="text"
                                        key={activeSlide}>
                                        <div className="text">
                                            <h1 className="small-title">{slide.smallTitle}</h1>
                                            <h1 className="big-title">
                                            <span className="main-color-2">{slide.bigTitleC1}</span>&nbsp;
                                            <span className="main-color-1">{slide.bigTitleC2}</span>
                                            </h1>
                                            <p className="description">
                                                {slide.description}
                                            </p>
                                        </div>
                                    </CSSTransition>
                                </TransitionGroup>
                                <TransitionGroup>
                                    <CSSTransition
                                        timeout={1000}
                                        classNames="buttons"
                                        key={activeSlide}>
                                        <div className="buttons">
                                            <a className="default-btn" target="_blank" rel="noopener noreferrer"
                                                href={`https://wa.me/573214682422?text=${slide.order}`}>
                                                Ordenar Ahora
                                            </a>
                                        </div>
                                    </CSSTransition>
                                </TransitionGroup>
                            </div>
                            {/* Social Networks */}
                            <div className="social-networks">
                                <a href="https://www.instagram.com/lechonalechoncitoscol/" target="_blank" rel="noopener noreferrer" title="Instagram">
                                    <img src={require('assets/img/social/instagram.svg')} className="social-icon" alt="Instagram"/>
                                </a>
                                <a href="https://www.facebook.com/Lechonalechoncitos" target="_blank" rel="noopener noreferrer" title="Facebook">
                                    <img src={require('assets/img/social/facebook.svg')} className="social-icon" alt="Facebook"/>
                                </a>
                                <a href="https://wa.me/573214682422?text=Hola, estoy interesado en..." target="_blank" rel="noopener noreferrer" title="Whatsapp">
                                    <img src={require('assets/img/social/whatsapp.svg')} className="social-icon" alt="Maps"/>
                                </a>
                            </div>
                        </div>
                        {/* Right */}
                        <div className="right">
                            <div className="slider">
                                <TransitionGroup>
                                    <CSSTransition
                                        timeout={1000}
                                        classNames="image"
                                        key={activeSlide}>
                                        <div className="container-img-slide">
                                            <img src={require(`assets/img/${slide.image}`)} className="img-slide" alt="Imagen"/>
                                        </div>
                                    </CSSTransition>
                                </TransitionGroup>
                                <div className="controls">
                                    <div className="lc-placeholder">
                                        {/* // */}
                                    </div>
                                    <div className="arrows">
                                        <div
                                            onClick={() => changeActiveSlide('prev')} 
                                            className="arrow" title="Anterior">
                                            <span className="material-icons arrow-left">arrow_back_ios</span>
                                        </div>
                                        <div 
                                            onClick={() => changeActiveSlide('next')}  
                                            className="arrow arrow-active" title="Siguiente">
                                            <span className="material-icons">arrow_forward_ios</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="slider-indicators">
                                    {
                                        dataSlider.map((slide, index) => (
                                            <span 
                                                className={`indicator ${activeSlide === index ? 'indicator-active' : ''}`}
                                                key={index}
                                                onClick={() => changeActiveSlide(index)}
                                            >
                                            </span>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                ))
            }
        </div>
    )
}

MainSlider.propTypes = {
    dataSlider: PropTypes.array
}

// C1 => Color 1, C2 => Color 2
MainSlider.defaultProps = {
    dataSlider: [
        {
            smallTitle: 'Try Our New',
            bigTitleC1: 'Cheezy',
            bigTitleC2: 'Burger',
            description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Incidunt ab sed unde?',
            image: 'main-slider/default-image.png',
            order: '¡Hola!, Estoy interesado en un platillo de Lechoncitos.'
        },
        {
            smallTitle: 'Try Our New 2',
            bigTitleC1: 'Meat',
            bigTitleC2: 'Creep',
            description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Incidunt ab sed unde?',
            image: 'main-slider/default-image.png',
            order: '¡Hola!, Estoy interesado en un platillo de Lechoncitos 2'
        }
    ]
}

export default MainSlider