import React, { useState, useEffect } from 'react'
import Footer from 'components/Footer'
// css
import './about-us.scss'

const AboutUsPage = () => {
    const [ animationClass, setAnimationClass ] = useState('')
    useEffect(() => {
        setAnimationClass('item-animation');
    }, [])

    return (
        <>
            <div className="about-us-page df-padding-2">
                <div className="about-us-container lc-container">
                    <h1 className="lc-default-title mt-15">
                    <span className="highlighted">¿</span>Lechoncitos <span className="highlighted">?</span>
                        <span className="back">¿Lechoncitos?</span>
                    </h1>
                    <div className="text-container">
                        <div className={`item ${animationClass}`}>
                            <h2 className="title">¿Por qué deberías <span>Probar?</span></h2>
                            <p>
                                Más allá de porque probar nuestros platos, queremos que nuestros clientes conozcan que 
                                siempre nos enfocamos en entregar nuestra lechona y platos típicos con el mayor rango en 
                                calidad posible; estamos certificados por el registro invima. Y además de eso, nuestra 
                                lechona es una de las favoritas en Santander ya que hemos sido escogidos por dos años 
                                consecutivos como ganadores lechona master. 
                            </p>
                        </div>
                        <div className={`item ${animationClass}`}>
                            <h2 className="title">¿Por qué deberías <span>Preferirnos?</span></h2>
                            <p>
                                En Lechonchitos llevamos más de 25 años entregándoles la mejor lechona a Santander y sus 
                                alrededores. Nuestro producto es de calidad y cuenta con todos los estándares requeridos 
                                para hacer de nuestros productos platos auténticos, de calidad y de sabor inigualable. 
                                Por ese motivo en Santander nos escogieron como el número 1.
                            </p>
                        </div>
                        <div className={`item ${animationClass}`}>
                            <h2 className="title"><span>Misión</span></h2>
                            <p>
                                Nuestro nombre representa un pasado lleno de historia familiar y tradiciones ricas en gastronomía que hoy en día se ve reflejado en nuestro restaurante y fábrica. Por ello, nuestra misión es satisfacer los paladares a a todos los comensales de la lechona y comida típica santandereana. Somos especializados en ofrecer menús para eventos, reuniones empresariales, bodas, quince años y mucho más. Nuestro compromiso es cada día entregar Lechona y platos típicos de calidad y hacer a muchas más familias felices.
                            </p>
                        </div>
                        <div className={`item ${animationClass}`}>
                            <h2 className="title"><span>Visión</span></h2>
                            <p>
                                Ser reconocidos como una de las lechonerias y platos típicos de mayor calidad a nivel nacional. De igual forma, estaremos ubicados en diferentes puntos estrategicos de la ciudad donde nuestros clientes no encuentrarán mucho más cerca. Y por último pero no la menos importante, seguir siendo los número uno por calidad, autenticidad y sabor inigualable.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
            <br />
            <br />
            <Footer />
        </>
    )
}

export default AboutUsPage