import React from 'react'
import PropTypes from 'prop-types'
// css
import './plate.scss'

const Plate = ({ plate }) => {
    const { image, title, description, order, price } = plate
    return (
        <div className="plate">
            <div className="container-image">
                <img src={require(`assets/img/menu/${image}`)} className="image" alt={title}/>
            </div>
            <div className="content">
                <h1 className="title">{title}</h1>
                <p className="description">{description}</p>
                <div className="bottom">
                    <a href={`https://wa.me/573214682422?text=${order}`} target="_blank" rel="noopener noreferrer" className="btn-order">
                        Ordenar Ahora
                    </a>
                    <h5 className="price">$ {price}</h5>
                </div>
            </div>
        </div>
    )
}

Plate.propTypes = {
    plate: PropTypes.object
}

Plate.defaultProps = {
    plate: {
        image: 'default-plate.png',
        title: 'Cheezy Bluster 2',
        description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Itaque minima accusantium cumque veritatis, magnam modi!',
        order: 'Disfruta de este fascinante platillo',
        price: '415.000'
    }
}

export default Plate