import React, { useState } from 'react'
import LazyLoad from 'react-lazyload'
import Footer from 'components/Footer'
// data
import galleryData from 'data/gallery.json'
// css
import './gallery.scss'

const GalleryPage = () => {
    const [wGallery, setWGallery] = useState(galleryData);
    const [itemBgImage, setBgImage] = useState({});
    const [showBgImage, setShowBgImage] = useState(false);
   
    const changeBackgroundImage = (options) => {
        const position = typeof(options.position) === 'undefined' ? 0 : options.position;
        let new_WGallery = [...wGallery];

        if (options.showBgImage) {
            const selectedImage = new_WGallery[position];
            new_WGallery.splice(position, 1)
            new_WGallery.unshift(selectedImage);

            setBgImage({
                src: options.src,
                text: options.text,
                captionTitle: options.captionTitle,
                position: 0
            })
            setWGallery(new_WGallery);
            toggleBgImage();
        } else { 
            let src = options.src,
                new_position = position;

            if (position >= itemBgImage.position || position === "left") {
                // move to left
                const firstImage = new_WGallery[0];
                new_WGallery.splice(0, 1);
                new_WGallery.push(firstImage);
            } else if (position <= itemBgImage.position || position === "right") {
                // move to rigth
                const lastImage = new_WGallery[new_WGallery.length - 1];
                new_WGallery.splice((new_WGallery.length - 1), 1);
                new_WGallery.unshift(lastImage);
            }

            if (position === "left" || position === "right") {
                src = require(`assets/img/${new_WGallery[itemBgImage.position].src}`);
                new_position = itemBgImage.position;
            }

            setWGallery(new_WGallery);
            setBgImage({
                src: src,
                position: new_position
            })    
        }
    }


    const toggleBgImage = (options) => {
        if (options) {
            if (options.resetGallery) {
                setWGallery(galleryData);
            }
        }
        setShowBgImage(!showBgImage)
    }

    return (
        <>
            <div className="gallery-container df-padding-2">
                <div className="gallery lc-container">
                    <h1 className="lc-default-title gallery-title mt-15">
                        Vivamos juntos <span className="highlighted">los mejores</span><br/>
                        <span className="hashtag">#momentosLechoncitos</span>
                    </h1>
                    <div className="items">
                        {
                            galleryData.map((item, index) => ( 
                                <LazyLoad 
                                    height={200}
                                    key={index}>
                                <div
                                    onClick={() => { 
                                        changeBackgroundImage({ 
                                            src: require(`assets/img/${item.src}`),
                                            captionTitle: item.captionTitle,
                                            text: item.text, 
                                            showBgImage: true,
                                            position: index
                                        }) 
                                    }} 
                                    className="item">
                                    <div
                                        style={{ backgroundImage: `url(${require(`assets/img/${item.src}`)})` }} 
                                        className="item-bg">
                                        <div className="caption">
                                        {/* Caption title */}
                                            {
                                                item.captionTitle ? 
                                                    <h3 className="caption-title">{ item.captionTitle }</h3>
                                                : <h3 className="caption-title">Sencillamente la Mejor</h3>
                                            }
                                            <p className="see-more">- Ver Más -</p>
                                        </div>
                                        {/* Text */}
                                        <button type="button" className="btn-see-more">
                                            Ver más
                                        </button>
                                    </div>
                                </div>
                                </LazyLoad>
                            ))
                        }
                    </div>
                </div>
                {/* Image Full */}
                <div className={`container-image-full ${showBgImage ? 'show-container-image-full' : ''}`}>
                    <span
                        onClick={() => { toggleBgImage({ resetGallery: true }) }} 
                        type="button" className="material-icons btn-close">close</span>
                    <div
                        style={{ backgroundImage: `url(${itemBgImage.src})` }} 
                        className="image">
                        <div className="text">
                            <h1 className="title">{ itemBgImage.captionTitle }</h1>
                            {
                                itemBgImage.text ?
                                    <p className="description">
                                        { itemBgImage.text }
                                    </p>
                                : null
                            }
                        </div>

                        <div 
                           onClick={(() => {
                            changeBackgroundImage({
                                position: "right"
                            })
                        })}
                            className="material-icons arrow arrow-left" title="Anterior">arrow_back_ios</div>
                        <div 
                            onClick={(() => {
                                changeBackgroundImage({
                                    position: "left"
                                })
                            })}
                            className="material-icons arrow arrow-right" title="Siguiente">arrow_forward_ios</div>
                        <div className="items-thumbnail">
                            {
                                wGallery.map((item, index) => (
                                    <div key={index} 
                                        style={{ backgroundImage: `url(${require(`assets/img/${item.src}`)})` }} 
                                        onClick={() => { 
                                            changeBackgroundImage({ 
                                                src: require(`assets/img/${item.src}`),
                                                position: index
                                            }) 
                                        }} 
                                        className="thumbnail"></div>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
            <br/>
            <Footer />
        </>
    )
}

export default GalleryPage